@import "../../variables";

.defaultButton {
    padding: 10px;
    background-color: $color-2;
    border: 2px solid black;
    box-shadow: 3px 3px black;
    font-weight: bold;
    font-size: 11pt;
    cursor: pointer;
}

.defaultButton:hover {
    background-color: $color-1;
    color: $color-2;
}