.navbar {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  z-index: 3;
  color: black;
  display: flex;
  transition: all 1s ease; }
  .navbar .wrapper {
    overflow: hidden;
    padding: 10px 30px;
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: space-between; }
    .navbar .wrapper .right {
      position: absolute;
      right: 20px;
      padding: 10px;
      background-color: #ffffff;
      border-radius: 100%;
      border: 2px solid #000000;
      box-shadow: 3px 3px;
      transition: all 0.5s ease;
      cursor: pointer; }
      .navbar .wrapper .right:hover {
        background-color: #F43731;
        transition: all 0.5s ease; }
        .navbar .wrapper .right:hover .hamburger .hamburgerLine,
        .navbar .wrapper .right:hover .hamburger .hamburgerLine::before,
        .navbar .wrapper .right:hover .hamburger .hamburgerLine::after {
          background-color: #ffffff;
          transition: all 0.5s ease; }
      .navbar .wrapper .right .hamburger {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 32px;
        height: 25px;
        cursor: pointer;
        transition: all .5s ease-in-out;
        overflow-x: visible; }
        .navbar .wrapper .right .hamburger .hamburgerLine {
          width: 100%;
          height: 3px;
          background-color: #000000;
          border-radius: 10px;
          transition: all .5s ease-in-out; }
        .navbar .wrapper .right .hamburger .hamburgerLine::before,
        .navbar .wrapper .right .hamburger .hamburgerLine::after {
          content: '';
          position: absolute;
          width: 100%;
          height: 3px;
          background-color: #000000;
          border-radius: 10px;
          transition: all .5s ease-in-out; }
        .navbar .wrapper .right .hamburger .hamburgerLine::before {
          -webkit-transform: translateY(-8px);
                  transform: translateY(-8px); }
        .navbar .wrapper .right .hamburger .hamburgerLine::after {
          -webkit-transform: translateY(8px);
                  transform: translateY(8px); }
  .navbar.active .wrapper .right .hamburger .hamburgerLine {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    background: transparent; }
  .navbar.active .wrapper .right .hamburger .hamburgerLine::before {
    -webkit-transform: rotate(45deg) translate(35px, -35px);
            transform: rotate(45deg) translate(35px, -35px); }
  .navbar.active .wrapper .right .hamburger .hamburgerLine::after {
    -webkit-transform: rotate(-45deg) translate(35px, 35px);
            transform: rotate(-45deg) translate(35px, 35px); }

