@import "../../variables";

#contact {
    height: 100%;
    display: block;
    padding-top: 5%;

    .contactHeader {
        height: 10%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;

        p {
            // font-family: 'Poppins', sans-serif; 
            font-weight: 700;
            text-transform: uppercase;
            font-size: 15pt;
        }
    }

    .contactEmail {
        width: 100%;
        height: 60%;
        display: flex;
        justify-content: center;
        align-items: center;

        .bigEmail {
            text-decoration: none;
            color: $color-1;
            text-transform: uppercase;
            font-size: 50pt;
            font-weight: 700;
            font-family: 'Poppins';
            line-height: 1;

            .at {
                color: $color-3;
                font-size: 60pt;
            }

            .dot {
                color: $color-3;
                font-size: 80pt;
            }

            &:hover {
                text-decoration: underline;
                text-decoration-color: $color-3;
            }
        }
    }

    .social {
        height: 25%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 20px;
        a {
            color: $color-1;

            &:hover {
                color: $color-3;
            }
        }
    }

    .footerContainer {
        height: 5%;

        .footerCredits {
            position: relative;
            float: right;
            padding-right: 2%;

            a {
                text-decoration-color: $color-3;
                color: $color-1;
            }
        }
    }
}

@media only screen and (max-width: 600px) {
    #contact {
        .contactEmail {
            .bigEmail {
                font-size: 50px;
                line-height: 1;
                .at {
                    font-size: 50px;
                    line-height: 0;
                }
                .dot {
                    line-height: 0;
                }
            }

        }
    }
    .at::before {
        content: "\a";
        white-space: pre;
    }
}